import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { LayoutComponent } from './layout/layout.component';
import { ErrorComponent } from './shared/error/error.component';
import { LoginComponent } from './auth/login/login.component';
import { RegisterComponent } from './auth/register/register.component';
import { BuyComponent } from './buy/buy.component';

import { ApplicationAuthGuard } from './auth-gaurd.service';

const routes: Routes = [
	{
		path: '',
		component: LayoutComponent,
		children: [{
			path: '',
			loadChildren: () => import('./dashboard/dashboard.module').then(m => m.DashboardModule)
		}, {
			path: 'invoices',
			loadChildren: () => import('./invoice/invoice.module').then(m => m.InvoiceModule)
		}, {
			path: 'contracts',
			loadChildren: () => import('./my-contracts/my-contracts.module').then(m => m.MyContractsModule)
		}, {
			path: 'products',
			loadChildren: () => import('./products/products.module').then(m => m.ProductsModule)
		},{
			path: 'profile',
			loadChildren: () => import('./auth/profile/profile.module').then(m => m.ProfileModule)
		},{
			path: 'organization',
			loadChildren: () => import('./auth/organization/organization.module').then(m => m.OrganizationModule)
		}]
	},
	{
		path: 'login',
		component: LoginComponent
	},
	{
		path: 'plan/:planId/buy',
		component: BuyComponent
	},
	{
		path: 'product/:productId/plan/:planId',
		component: RegisterComponent
	},
	{
		path: 'error',
		component: ErrorComponent
	},
	{
		path: '**',
		redirectTo: 'error'
	}
];

@NgModule({
	imports: [RouterModule.forRoot(routes)],
	exports: [RouterModule]
})
export class AppRoutingModule { }
