import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { MyContractsComponent } from './my-contracts.component';

const routes: Routes = [
	{
		path: '',
		component: MyContractsComponent
	}, {
		path: ':code/view-details',
		loadChildren: () => import('./view-contract/view-contract.module').then(m => m.ViewContractModule)
	}
];

@NgModule({
	imports: [RouterModule.forChild(routes)],
	exports: [RouterModule]
})

export class MyContractsRoutingModule { }